import React from "react";
import './contacts.css';

const ContactsSection = () => {
    return(
        <section className="section__contact" id="section__contact">
            <div className="container">
                <div className="row">
                    <div className="col-sm-12">
                        <div className="section_title__body">
                            <div className="section__subtitle dark__subtitle">
                                Информация о <span>нас</span>
                            </div>
                            <h2 className="section__title dark__title">
                            </h2>
                            <p className="section_title__desc">
                            </p>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col-sm-6">
                        <h3>Регистрация на курсы</h3>
                        <div className="alert" id="form_message" role="alert"></div>

                        <form id="form_sendemail">

                            <div className="form-group">
                                <label htmlFor="email" className="sr-only">ФИО</label>
                                <input type="email" name="email" className="form-control" id="Фио"
                                       placeholder="Укажите ФИО" />
                                <span className="help-block"></span>
                            </div>

                            <div className="form-group">
                                <label htmlFor="email" className="sr-only">Email</label>
                                <input type="email" name="email" className="form-control" id="email"
                                       placeholder="Укажите свой email адрес" />
                                    <span className="help-block"></span>
                            </div>

                            <div className="form-group">
                                <label htmlFor="name" className="sr-only">Курс</label>
                                <input type="text" name="name" className="form-control" id="course"
                                       placeholder="Укажите назание курса" />
                                    <span className="help-block"></span>
                            </div>

                            <div className="form-group">
                                <label htmlFor="message" className="sr-only">Сообщение</label>
                                <textarea name="message" className="form-control" id="message" rows="6"
                                          placeholder="Введите текст сообщения"></textarea>
                                <span className="help-block"></span>
                            </div>

                            <div className="form-group">
                                <small className="text-muted">
                                    * Все поля обязательны.
                                </small>
                            </div>

                            <button type="submit" className="btn btn-block btn-accent">
                                Отправить сообщение
                            </button>

                        </form>

                    </div>
                    <div className="col-sm-6">
                        <div className="contact_info__body">
                            <h3>Остались вопросы?</h3>
                            <div className="contact_info__item">
                                <div className="contact__title">
                                    Email<span>.</span>

                                </div>
                                <div className="contact__info">
                                    <div className="contact_info__wrapper">
                                        <a href="mailto:education@solarl.ru">education@solarl.ru</a>
                                    </div>
                                </div>
                            </div>
                            <div className="contact_info__item">
                                <div className="contact__title">
                                    VK<span>.</span>
                                </div>
                                <div className="contact__info">
                                    <div className="contact_info__wrapper row">
                                        <div className="col-md-6">
                                            <a href="https://vk.com/tanya_pichugova" target="_blank" rel="noopener noreferrer"><img style={{borderRadius: '50%'}} width="100" src="/assets/img/tanya.jpg" alt="Татьяна" /></a>
                                        </div>
                                        <div className="col-md-6">
                                            <a href="https://vk.com/lichi_alice" target="_blank" rel="noopener noreferrer"><img style={{borderRadius: '50%'}}  width="100" src="/assets/img/alice.jpg" alt="Алиса" /></a>
                                        </div>


                                    </div>
                                </div>
                            </div>
                            <div className="contact_info__item">
                                <div className="contact__title">
                                    Адрес<span>.</span>
                                </div>
                                <div className="contact__info">
                                    <div className="contact_info__wrapper">
                                        <p><a rel="noopener noreferrer" href='https://yandex.ru/maps/-/CKAxY2yL' target='_blank'> Севастополь, ул.Большая Морская, 23</a></p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default ContactsSection;